"use client"

import { useState } from "react"
import Link from "next/link"
import { usePathname } from "next/navigation"
import { 
  Home,
  PenTool,
  Coins,
  FileText,
  Volume2,
  MessageSquare,
  Send,
  Wallet,
  History,
  Activity,
  FileCode2,
  LogOut
} from "lucide-react"

const menuItems = [
  { name: "Main", icon: Home, href: "/" },
  { name: "Launch", icon: PenTool, href: "/launch" },
  { name: "New Token", icon: Coins, href: "/new-token" },
  { name: "Vanity Address", icon: FileText, href: "/vanity-address" },
  { name: "Volume", icon: Volume2, href: "/volume" },
  { name: "Comment", icon: MessageSquare, href: "/comment" },
  { name: "Telegram", icon: Send, href: "/telegram" },
  { name: "Wallets", icon: Wallet, href: "/wallets" },
  { name: "Transactions", icon: History, href: "/transactions" },
  { name: "Status", icon: Activity, href: "/status" },
  { name: "Template Pages", icon: FileCode2, href: "/templates" },
  { name: "Logout", icon: LogOut, href: "/logout" }
]

export default function Sidebar() {
  const pathname = usePathname()
  
  return (
    <div className="w-64 bg-[#0f172a] text-white border-r border-gray-800">
      <div className="p-4">
        <h1 className="text-2xl font-bold mb-8 text-blue-500">AI Panel</h1>
        <nav className="space-y-1">
          {menuItems.map((item) => {
            const isActive = pathname === item.href
            const Icon = item.icon
            
            return (
              <Link
                key={item.name}
                href={item.href}
                className={`flex items-center gap-3 px-3 py-2 rounded-lg transition-colors ${
                  isActive 
                    ? "bg-blue-600/50 text-blue-500 font-medium" 
                    : "text-gray-400 hover:bg-gray-800/50 hover:text-gray-200"
                }`}
              >
                <Icon className="w-5 h-5" />
                <span>{item.name}</span>
              </Link>
            )
          })}
        </nav>
      </div>
    </div>
  )
}
